class SideNav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      openKeys: ['sub1'],
      navUpdateModalVisible: false,
      navUpdateModalType: '1', // 1 一级导航， 2， 二级导航
    };
  }

  rootSubmenuKeys = ['sub1', 'sub2', 'sub4'];

  onOpenChange = openKeys => {
    const latestOpenKey = openKeys.find(
      key => this.state.openKeys.indexOf(key) === -1,
    );
    if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  };

  showNavUpdateModal = navUpdateModalType => {
    this.setState({
      navUpdateModalType,
      navUpdateModalVisible: true,
    });
  };

  closeNavUpdateModal = data => {
    this.setState({
      navUpdateModalVisible: false,
    });
  };

  handleClick = item => {
    const { key } = item;
    if (key === 'add') {
      this.showNavUpdateModal('1');
    } else if (/addSub/.test(key)) {
      this.showNavUpdateModal('2');
    }
    console.log(item, key);
  };

  remove = (navType, menu) => {
    confirm({
      title: `删除${navType === '1' ? '一' : '二'}级导航`,
      content: '删除菜单后，子菜单也会被删除',
      okText: '确认删除',
      cancelText: '我再想想',
      onOk() {
        console.log('OK');
      },
    });
  };

  render() {
    const { navUpdateModalVisible, navUpdateModalType } = this.state;

    return (
      <div className={s.sideNav}>
        <Menu
          onClick={this.handleClick}
          mode="inline"
          openKeys={this.state.openKeys}
          onOpenChange={this.onOpenChange}
        >
          {nav.map((n, i) => (
            <SubMenu
              key={n.key + i}
              title={
                <span
                  onDoubleClick={() => {
                    this.showNavUpdateModal('1', n);
                  }}
                >
                  {n.title}
                </span>
              }
            >
              {n.content.map((c, j) => (
                <Menu.Item key={c.value + j}>
                  <span
                    onDoubleClick={() => {
                      this.showNavUpdateModal('2', c);
                    }}
                  >
                    {c.label}
                  </span>
                </Menu.Item>
              ))}
            </SubMenu>
          ))}
        </Menu>
      </div>
    );
  }
}

export default withStyles(s)(SideNav);
