import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import MainNav from './components/MainNav';
import HotSpots from './components/HotSpots';
import Banner from './components/Banner';
import SideNav from './components/SideNav';
import ThumbList from './components/ThumbList';
import ToggleLayer from './components/ToggleLayer';
import Topbar from './components/Topbar';
import Navbar from './components/Navbar';

import storage from 'utils/localStorage';

import s from './Topic.less';

class Topic extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  constructor(props) {
    super(props);
  }

  componentDidMount() {
    storage.set('topicModules', JSON.stringify(this.modulesValue));
  }

  modulesValue = {
    title: '1',
    banner: '1',
    sideNav: '1',
    hotSpots: '1',
    mainNav: '1',
  };

  changeModules = (key, checked) => {
    const value = checked ? '1' : '0';
    // let values = Object.assign({}, this.modulesValue, { [key]: value });
    this.modulesValue[key] = value;

    storage.set('topicModules', JSON.stringify(this.modulesValue));
  };

  render() {
    const toggleLayerProps = {
      targetName: 'title',
      onChange: this.changeModules,
    };

    return (
      <div className={s.root}>
        <Topbar />
        <Navbar toggleLayerProps={toggleLayerProps} />
        <div className="container">
          <ToggleLayer targetName="banner" onChange={this.changeModules}>
            <Banner />
          </ToggleLayer>
        </div>
        <div className="container">
          <div className={s.body}>
            <ToggleLayer targetName="sideNav" onChange={this.changeModules}>
              <SideNav />
            </ToggleLayer>
            <div className={s.main}>
              <ToggleLayer targetName="mainNav" onChange={this.changeModules}>
                <MainNav />
              </ToggleLayer>
              <ToggleLayer targetName="hotSpots" onChange={this.changeModules}>
                <HotSpots />
              </ToggleLayer>
              <ThumbList row={4} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Topic);
