import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { Row, Col, Radio, Tabs, Pagination } from 'antd';
import s from './ThumbList.less';

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
const TabPane = Tabs.TabPane;

const images = [];

for (let i = 0; i < 60; i++) {
  images.push(i);
}

class ThumbList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };
  }

  render() {
    const { row } = this.props;
    const Pager = <Pagination simple defaultCurrent={2} total={50} />;

    const spanSize = row;

    return (
      <div className={s.thumbList}>
        <Tabs
          defaultActiveKey="t1"
          size="large"
          animated={false}
          tabBarExtraContent={Pager}
        >
          <TabPane
            tab={
              <span>
                单张<small>（121212）</small>
              </span>
            }
            key="t1"
          >
            {images.map((img, index) => (
              <Col span={spanSize} key={`img${index}`}>
                <div className={s.thumbItem}>
                  <div className={s.picture}>
                    <img
                      src="http://bj-feiyuantu.oss-cn-beijing.aliyuncs.com/creative/vcg/176/new/dbb95ca28a7f46cfb71aed3dacb2606f.jpg"
                      alt=""
                    />
                  </div>
                  <h5 className={s.title}>2018-02-09 22:43:32</h5>
                  <p className={s.caption}>
                    ID：VCG111147942295 <br />2352x3600px
                  </p>
                </div>
              </Col>
            ))}
          </TabPane>
          <TabPane
            tab={
              <span>
                组照<small>（11）</small>
              </span>
            }
            key="t2"
          >
            {images.map((img, index) => (
              <Col span={spanSize} key={`group${index}`}>
                <div className={s.thumbItem}>
                  <div className={s.picture}>
                    <img
                      src="https://goss.vcg.com/editorial/vcg/400/new/VCG31N909929638.jpg"
                      alt=""
                    />
                  </div>
                  <h5 className={s.title}>2018-02-09 22:43:32</h5>
                  <p className={s.caption}>
                    2018秋冬纽约男装周：David Hart 品牌秀场
                  </p>
                </div>
              </Col>
            ))}
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default withStyles(s)(ThumbList);
